body {
  background-color: @color-black;
  color: @color-white;
  font-family: @main-font;
}

html {
    scroll-behavior: smooth;
}

.container {
  max-width: 1172px;
  margin: 0 auto;
  padding: 0 15px;
}

.btn {
  max-width: 300px;
  width: 100%;
  height: 70px;
  background-color: @color-mako-05;
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  color: @color-white;
  .transition (@prop, @time, @ease);

  @media screen and (@max576) {
    font-size: 17px;
    line-height: 20px;
    height: 60px;
  }

  &:hover {
    background-color: @color-buttered-rum;
    color: @color-black;
  }
}

.line {
  width: 100%;
  max-width: 300px;
  height: 1px;
  background-color: @color-white;
  display: block;
}

.cross {
  width: 25px;
  height: 25px;
  right: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;

  &::after,
  &::before {
    content: "";
    position: absolute;
    background-color: @color-white;
    width: 25px;
    height: 2px;
    .transition (@prop, @time, @ease);
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }

  &:hover::after {
    transform: rotate(45deg);
  }

  &:hover::before {
    transform: rotate(-45deg);
  }

  &_small {
    width: 10px;
    height: 10px;

    &::after,
    &::before {
      width: 10px;
    }
  }
}

.modal-header {
  display: flex;
  justify-content: space-between;

  &__heading h3 {
    font-weight: 700;
    font-size: 23px;
    line-height: 27px;
    letter-spacing: 0.02em;
  }

  &__heading h5 {
    letter-spacing: 0.02em;
    font-size: 17px;
    font-weight: 500;
    line-height: 21px;
  }
}

.error-message {
  font-family: @sub-font;
  padding: 5px;
  color: @color-red;
  font-size: 13px;
  font-weight: 400;
}

.btn-show-password {
  font-size: 22px;
  width: fit-content;
  margin-left: auto;
  position: relative;
  bottom: 35px;
  height: 0;

  &_show {
    &::before {
      content: "";
      background-color: #000;
      width: 18px;
      height: 2px;
      position: absolute;
      top: 15px;
      left: 2px;
      transform: rotate(-45deg);
    }
  }
}
