.registration {
  padding-top: 70px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100vh;
  background-color: @color-black;
  justify-content: center;
  align-items: center;
  display: none;

  &_active {
    display: flex;
  }

  &__wrapper {
    display: flex;
    width: 100%;
    max-width: 910px;
  }

  &__form {
    display: flex;
    flex-direction: column;
    max-width: 460px;
    width: 100%;
    background-color: @color-white;
    padding: 40px;

    @media screen and (@max576) {
      margin: 0 auto;
    }

    @media screen and (@max320) {
      padding: 10px;
    }
  }

  &__input {
    padding: 5px;
    margin-top: 15px;
    border-top: none;
    border-left: none;
    border-right: none;
    border-bottom: 1px solid @color-black-05;

    &:first-child {
      margin-top: 0;
    }

    &::placeholder {
      font-size: 13px;
      font-family: @sub-font;
    }
  }

  &__image-inner {
    background-image: url("../../../../media/images/form-image.webp");
    max-width: 460px;
    width: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;

    &::before {
      content: "";
      width: 100%;
      height: 100%;
      background-color: @color-black-05;
      position: absolute;
    }

    @media screen and (@max576) {
      display: none;
    }
  }

  &__button {
    height: 50px;
    max-width: 100%;
    background-color: @color-black;
    border-radius: 3px;
    margin-top: 20px;
  }

  &__label {
    color: @color-black;
    .transition (@prop, @time, @ease);
    cursor: help;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;

    @media screen and (@max576) {
      font-size: 12px;
    }

    &:hover {
      opacity: 0.8;
    }
  }

  &__checkbox {
    width: fit-content;
    cursor: pointer;
    margin: 20px 0 3px 0;
  }
}

.datalist {
  width: 100%;
  background-color: @color-white;
}
